<template>
  <div id="app">
      <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  created(){
  	
  	
  	
  },
  mounted(){
  	   if(this.getCookie("sessionId").length>0){
  	   
  	   	this.$store.state.sessionId=this.getCookie("sessionId")
  	   	this.$router.push({
  	   		name:"Home"
  	   	})
  	   }else{
  	   	   this.setCookie("sessionId","",15)
  	   	   	this.$router.push({
  	   		name:"Binding"
  	   	})
  	   }
        this.$Api.AdsList({"s":this.$store.state.sessionId}).then((res)=>{
        	console.log(res)
        })

  	
  },
  methods:{
  	 getCookie(cname) {
				var name = cname + "=";
				var ca = document.cookie.split(';');
				for(var i = 0; i < ca.length; i++) {
					var c = ca[i].trim();
					if(c.indexOf(name) == 0) return c.substring(name.length, c.length);
				}
				return "";
			},

		 setCookie(cname, cvalue, exdays) {
				var d = new Date();
				d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
				var expires = "expires=" + d.toGMTString();
				document.cookie = cname + "=" + cvalue + "; " + expires;
			}
  }
}
</script>

<style>
body,html{
	height: 100%;
	background: #FFFFFF;
}	
	
 #app {
    height: 100%;
    background: #FFFFFF;
  }
</style>



