import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const Fontsize=document.documentElement.clientWidth/375
let nowYear=new Date().getFullYear()
let nowMonth=new Date().getMonth()+1

const store = new Vuex.Store({
	state:{
		sessionId:"",
		font:Fontsize,
		nowYear:nowYear,
		nowMonth:nowMonth
		
	}
	
	
	
	
	
	
	
})
export default store


