// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './sass/public.scss'
import layer from '@/plugin/layer/layer'
import Api from './api'
import store from './store/store'
import './assets/css/main.css';
import './plugin/mui/mui.css';
import './plugin/mui/mui.pickerall.css';
import './plugin/Toast/toast.css';
import Toast from './plugin/Toast/toast';
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI);


import Picker from './plugin/mui/mui.picker.min.js' 



Vue.use(layer);

Vue.use(Toast);

Vue.config.productionTip = false
Vue.prototype.$Api = Api;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



